.order__right-section {
	position: fixed;
	right: 0;
	top: 70px;
	width: calc(30% - 60px);
	box-shadow: -5px 0px 10px 1px #ddd;
	padding: 30px;
	text-align: right;
	height: 100%;
}

.order__right-section-content {
	width: 100%;
	line-height: 1.7;
	padding: 30px 0;
}

.orderTotal__title {
	letter-spacing: 0.5px;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
}

.order__total-list {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.order__total-list > * {
	margin: 0;
	flex: 0.2 1 40%;
}

.order__total-title {
	font-size: medium;
	font-weight: 600;
	letter-spacing: 0.9px;
	text-align: left;
}

.order__total-price {
	font-size: 1.4rem;
	letter-spacing: 0.7px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.order__total-list-text {
	font-size: 1rem;
	margin-left: 30%;
}

.order__total {
	margin-top: 10px;
	font-size: x-large;
	display: flex;
	justify-content: space-between;
	border-top: 1px solid lightgrey;
}

.order__total span {
	letter-spacing: 0.7px;
}

.order__total-btns {
	display: flex;
	margin: 0 auto;
	justify-content: space-around;
	flex-wrap: wrap;
}

.order__total-empty-button,
.order__total-checkout-button-link {
	margin: 0 5px;
	flex: 1 1 calc(50% - 20px);
}
.order__total-checkout-button {
	background: #d70f64;
}
.order__total-empty-button {
	background: black;
}

.order__total-checkout-button,
.order__total-empty-button {
	padding: 10px 20px;
	border: none;
	outline: none;
	color: white;
	font-size: 1.1rem;
	width: 100%;
	cursor: pointer;
	font-family: "Montserrat";
	transition: background 0.2s linear;
}

.order__total-checkout-button a {
	color: white;
}

.order__total-empty-button:hover,
.order__total-checkout-button:hover {
	background: dodgerblue;
}

.order__total-fee-text {
	font-size: small;
}

.order__total-fee-text > span {
	font-size: large;
	margin-left: 20px;
}

@media only screen and (max-width: 1024px) {
	.order__total-checkout-button,
	.order__total-empty-button {
		font-size: 1rem;
	}
}

@media only screen and (max-width: 850px) {
	.order__total-checkout-button,
	.order__total-empty-button {
		font-size: 0.9rem;
	}
}

@media only screen and (max-width: 773px) {
	.order__total-checkout-button,
	.order__total-empty-button {
		font-size: 0.8rem;
	}
}

@media only screen and (max-width: 700px) {
	.order__right-section {
		position: relative;
		top: auto;
		width: calc(100% - 60px);
	}
}
